import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatTableDataSource } from '@angular/material/table';
import { FormListComponent } from '@fragments';
import { ApiService, SweetAlertService } from '@services';
import { ENDPOINTS, STATUSES } from '@shared';
import { S1SettlementOrExpirationPeriod } from 'app/core/models/s1-settlement-or-expiration-period.model';
import { ValidateFieldService } from 'app/core/services/validate-field.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-s1-fees-settlement-period',
  templateUrl: './s1-fees-settlement-period.component.html',
  styleUrls: ['./s1-fees-settlement-period.component.scss']
})
export class S1FeesSettlementPeriodComponent implements OnInit {

  @ViewChild(FormListComponent) formListComponent: FormListComponent;
  dataSource: MatTableDataSource<S1SettlementOrExpirationPeriod>;
  displayedColumns: string[] = ['noOfHours', 'noOfDays'];
  statusList = [...STATUSES];

  s1FeeSettlementPeriod = new S1SettlementOrExpirationPeriod();
  s1FeeSettlementPeriodList = [];

  s1FeeSettlementPeriodForm: FormGroup;


  constructor(
    private apiService: ApiService,
    private formBuilder: FormBuilder,
    private sweetAlertService: SweetAlertService,
    public validateFieldService: ValidateFieldService,
  ) { }

  ngOnInit() {
    this.getList();
    this.setFormValidator();
  }

  getList(): void {
    this.apiService.findAll(ENDPOINTS.s1FeesSettlementPeriod).subscribe((res: any) => {
      this.s1FeeSettlementPeriodList = res.responseData.data;
      this.dataSource = new MatTableDataSource<S1SettlementOrExpirationPeriod>(res.responseData.data);
      this.dataSource.paginator = this.formListComponent.paginator;
      this.dataSource.sort = this.formListComponent.sort;
    });
  }

  save(): void {
    this.apiService.save(ENDPOINTS.s1FeesSettlementPeriod, this.s1FeeSettlementPeriod).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        if (!Number(this.s1FeeSettlementPeriod.noOfDays)
          || !Number(this.s1FeeSettlementPeriod.noOfHours)) {
          err.error.message = 'Please enter numeric values only';
        } else {
          err.error.message = 'Default Number of hours and days have already been set. Select the entry to update the record';
        }
        this.sweetAlertService.error(err);
      }, () => {
        this.resetFormValidator(),
          this.getList();
      }
    )
  }

  getS1FeeSettlementPeriod(): void {
    this.apiService.findById(ENDPOINTS.s1FeesSettlementPeriod, this.formListComponent.idForUpdate).subscribe((res: any) => {
      if (res) {
        this.s1FeeSettlementPeriod = res.responseData.data;
        console.log(res);

      }
    });
  }

  update(): void {
    this.apiService.update(ENDPOINTS.s1FeesSettlementPeriod, this.s1FeeSettlementPeriod).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {
        this.resetFormValidator()
        this.getList();
      });
  }

  setFormValidator(): void {
    this.s1FeeSettlementPeriodForm = this.formBuilder.group({
      noOfHours: ['', Validators.required],
      noOfDays: ['', Validators.required],
    });
  }

  resetFormValidator(): void {
    this.s1FeeSettlementPeriod = new S1SettlementOrExpirationPeriod();
    this.s1FeeSettlementPeriodForm.markAsUntouched();
    this.s1FeeSettlementPeriodForm.markAsPristine();
  }

  delete() {
    Swal.fire({
      text: 'Are you sure you want to delete this S1 fees settlement period?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirm'
    }).then((result) => {
      if (result.value) {
        this.apiService.deleteById(ENDPOINTS.s1FeesSettlementPeriod, this.s1FeeSettlementPeriod.id).subscribe(
          () => {
            this.sweetAlertService.customSuccessMessage('S1 fees settlement period successfully deleted.');
            this.resetFormValidator();
            this.getList();
          }
        );
      }
    });
  }
}
